nav {
    background-color: greenyellow;
    position:fixed;
    bottom:15px;
    left:15px;
    width: 30px;
    height: 30px;
    overflow:hidden;
    transition:height 0.5s, width 0.5s, left 0.5s, bottom 0.5s;
    ul.lis {
        .burger__li {
            opacity: 0;
        }
    }
    
    }
 
 nav.is-open {
     background-color:brown;
     position:fixed;
     bottom: 0;
     left:0;
     z-index: 1;
     width: 100%;
     height: 100%;
     opacity: 0.5;
     ul.lis{
         position:fixed;
         display:flex;
         flex-direction:column;
         justify-content:center;
         align-items:center;
         height: 100vh;
         width: 100vw;
         margin-bottom:20%;
       
       .burger__li {
           
           opacity:1;
         font-size: 40px;
         margin: -100px;
         
       }
       .burger__li:not(:last-child) {
         margin-bottom:0;
 }
     }
 }
 .banner {
    background-color: $primarycolor;
    width: 100vw;
    height: 50vh;
    position:absolute;
  .slogan {
    text-align:center;
    top: 30vh;
  }
  }
  .sentence {
    text-align: center;
    position: relative;
    top: 36vh;
    flex-flow: column wrap;
    color: white;
    .jackets {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        position: absolute;
        overflow: hidden;
        top: -4%;
        right: -64vw;
        width: auto;
        height: 100vh;
        .left__jacket {
            position: relative;
            width: auto;
            height: 43vh;
            transform: rotate(-25.79deg);
            top: -22px;
            left: 0px;
        }
        .right__jacket {
            position: relative;
            width: auto;
            bottom: 55vh;
            right: -2px;
            height: 45vh;
            transform: rotate(36deg);
        }
  }
}
 //  Choisir le mod√®le
.choose{
    // background-color: $thirdcolor;
    color: $primarycolor;
    text-align: center;
    margin-top: 52vh;
    padding-top:16px;
    padding-bottom: 15px;
      font-size: 24px;
      .button__choose{
        background-color: $primarycolor;
        border: none;
        color: #FFFFFF;
        height: 6vh;
        width: 48vw;
        font-size: 14px;
        border-radius: 20.5px;
        margin-top: 10px;
        margin-bottom: 10px;
        display: block;
        margin: auto;
        text-align: center;
        margin-bottom: 16px;
        box-shadow: 4px 4px 0px $thirdcolor;
      }
      .choose__sections{
        font-size: 18px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 24px;
      }
      .blue__jacket, .dark__jacket{
        height: 46vh;
        width: auto;
        padding-top: 16px;
      }
      .linkCustom{
        text-decoration: none;
     color:white;
      }
      .choose__price{
        color: black;
        font-size: 18px;
      }
      
      @media (min-width:750px) {
        .choose__title{
          font-size: 48px;
        }
        .choose__sections{
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          justify-self: center;
        }
        .button__choose{
          width: 18vw;
          height: 7.5vh;
          text-align: center;
          // margin-left: 55px;
          font-size: 24px;
          justify-self: center;
        }
        .choose__blue{
          padding-right: 160px;
        }
        .choose__price{
          font-size: 18px;
        }
      }
   }
   
